.MemberSigninMsg {
    width: 50%;
    margin: auto;
    margin-bottom: 50px;
    padding: 2rem;
    font-size: 1.2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.MemberSigninMsg h2 {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 2.5rem;
    color: var(--color-font-1);
}

.MemberSigninMsg p {
    font-size: 1.1rem;
    margin-top: 10px;
    margin-bottom: 0;
    color: var(--color-font-1);
}

.MemberSigninMsg.error p {
    color: var(--color-font-2);
}

.MemberSigninMsg.success p {
    color: #4361ee;
}

.MemberSigninMsg button {
    background-color: var(--color-box-1);
    border: none;
    border-radius: 8px;
    padding: .4rem 1.5rem;
    font-size: 1.2rem;
    margin-top: 10px;
    cursor: pointer;
    transition: background-color .3s ease;
}

.MemberSigninMsg button:hover,
.MemberSigninMsg button:active {
    background-color: #e41e2e;
}

.MemberSigninMsg button a {
    text-decoration: none;
    color: white;
}